const styles = (theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
});

class SignIn extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      usernameEmail: '',
      password: ''
    }

  }

  // componentDidMount() {

  //     const params = {
  //       usernameEmail: "AliceCustomer",
  //       password: "abcdefghijklmnopqrstuvwxyx"
  //     };
  //     // fetch(SERVER_URL + '/api/auth/login?' + encodeGetParams(params), { mode: 'no-cors'})
  //     //   .then(response => response.json())
  //     //   .then(data => console.log(data))
  //     //   .catch(error => console.log("error"));

  //     fetch(SERVER_URL + '/restaurants', { mode: 'no-cors'})
  //       .then(response => response.json())
  //       .then(data => console.log(data))
  //       .catch(error => console.log("error"));

  //     fetch(SERVER_URL + '/api/auth/login', {
  //         method: 'POST',
  //         mode: 'no-cors',
  //         headers: {
  //           'Content-Type': 'application/json'
  //         },
  //         body: JSON.stringify(params)
  //       })
  //       .then(response => response.json())
  //       .then(data => console.log(data))
  //       .catch(error => console.log("error"));

  // }

  login() {
    const { usernameEmail, password } = this.state;
    const params = {
        usernameEmail: usernameEmail,
        password: password
    };
    const req = new Request(`${SERVER_URL}/users/login`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ usernameEmail, password }),
    });
    fetch(req)
    // fetch(SERVER_URL + '/api/auth/login', {
    //   method: 'POST',
    //   mode: 'no-cors',
    //   headers: {
    //     'Content-Type': 'application/json'
    //   },
    //   body: JSON.stringify(params)
    // })
    .then(response => response.json())
    .then(data => {
      console.log(data);
      // window.location.assign('/restaurantlist')
    })
    .catch(error => {
      console.log("error");

    });

    this.setState({
      usernameEmail: '',
      password: ''
    });

    // if (TEST) {
    //   window.location.assign('/restaurantlist')
    // }
  }


  render() {
    const { classes } = this.props;
    return (
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign in
          </Typography>
          <div className={classes.form}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              label="Username"
              name="email"
              autoComplete="email"
              autoFocus
              value={this.state.usernameEmail}
              onChange={(e) => { this.setState({ usernameEmail: e.target.value })}}
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
              value={this.state.password}
              onChange={(e) => { this.setState({ password: e.target.value })}}
            />
            {/* <FormControlLabel
              control={<Checkbox value="remember" color="primary" />}
              label="Remember me"
            /> */}
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              onClick={this.login.bind(this)}
            >
              Sign In
            </Button>
            {/* <Grid container>
              <Grid item xs>
                <Link href="#" variant="body2">
                  Forgot password?
                </Link>
              </Grid>
              <Grid item>
                <Link href="/signup" variant="body2">
                  {"Don't have an account? Sign Up"}
                </Link>
              </Grid>
            </Grid> */}
            <Grid container justify="flex-end">
              <Grid item>
                <Link href="/signup" variant="body2">
                  {"Don't have an account? Sign Up"}
                </Link>
              </Grid>
            </Grid>
          </div>
        </div>
      </Container>
    );
  }
}

export default withStyles(styles)(SignIn);
